.authcardContainer {
  margin-top: 15vh;
  margin-bottom: 20vh;
}

.authcard {
  width: 40%;
  min-width: 350px;
  margin: auto;
  border: 4px white solid;
  border-radius: 20px;
  padding: 2vw;
  box-shadow: 0 0 7vw #0487F1;
  text-shadow: 0 0 1.5vw #12ccf4;
  background: rgba(39, 38, 102, 0.7);
}

.authcardTitle {
  text-shadow: 0 0 3vw #000000;
  font-size: 30px;
  color: white;
}

.authcardButton {
  color: white;
  background: rgba(6, 140, 255, 0.5);;
  font-size: 1.5rem;
  min-width: 100px;
  min-height: 30px;
  width: 160px;
  height: 40px;
  padding: 0.5vh;
  border-radius: 7px;
  margin: 5px;
}

.authcardButton:focus {
  background-color: rgba(6, 140, 255, 0.5) !important;
}


.authInput {
  color: white;
  border-bottom: 2px solid white !important;
  font-size: 20px !important;
}

.authInput:focus {
  box-shadow: 0px 0px 7px 5px #0487F1 !important;
  border: 2px white solid !important;
}

.authInput::placeholder {
  font-size: 20px;
  color: white !important;
  text-shadow: 2px 2px 7px black;
}

@media screen and (max-width: 600px) {
  .authcardContainer {
    margin: 80px 5px 25px 5px;
  }
  .authcard {
    text-align: center;
    width: 100%;
    height: 50%;
    min-width: auto;
    margin: auto;
    border: none;
    border-radius: 20px;
    padding: 40px 5px;
    box-shadow: 0 0 7vw #0487f1;
    text-shadow: 0 0 1.5vw #12ccf4;
    background: rgba(39, 38, 102, 0.7);
  }
  .authInput {
    margin-top: 40px !important;
    color: white;
    border-bottom: 1px solid white !important;
    font-size: 20px !important;
  }
  .authcardButton {
    margin-top: 40px;
  }
  .authInput:focus {
    box-shadow: 0px 0px 7px 5px #0487F1 !important;
    border: none !important;
    border-bottom: 1px solid white !important;
  }
}
